p, h1, h2, h3 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  font-family: DM Sans, sans-serif;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Regular.c108cb9e.woff"), url("DMSans-Regular.461ab9f9.woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Italic.73116179.woff"), url("DMSans-Italic.108082e4.woff2");
  font-weight: 400;
  font-display: swap;
  font-style: italic;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Medium.7d344570.woff"), url("DMSans-Medium.63db2800.woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Bold.8954ee88.woff2"), url("DMSans-Bold.a9858b2f.woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

:root {
  --light-theme-bcg: #f6f6f6;
  --light-theme-bcg-header: #fff;
  --ligth-theme-bcg-shoplist-block: #fff;
  --light-theme-primary-text-color: #111;
  --light-theme-second-text-color: #b4afaf;
  --light-theme-categories-text-color: rgba(17, 17, 17, .6);
  --dark-theme-bcg: #202024;
  --dark-theme-bcg-header: #111;
  --dark-theme-bcg-shoplist-block: #111;
  --dark-theme-primary-text-color: #fff;
  --dark-theme-second-text-color: #b4afaf;
  --dark-theme-categories-text-color: rgba(255, 255, 255, .6);
  --gradient-bcg-color: radial-gradient( 183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100% );
  --dump-bcg-color: #4f2ee8;
  --home-bcg-color: #eac645;
  --transition-func: cubic-bezier(.4, 0, .2, 1);
}

.container {
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.div-button {
  border: 1px solid var(--dump-bcg-color);
  width: 100px;
  color: var(--light-theme-primary-text-color);
  letter-spacing: .04em;
  text-align: center;
  cursor: pointer;
  border-radius: 4px;
  padding: 12px 24px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
}

.top-books-list {
  flex-wrap: wrap;
  display: flex;
}

.top-book-card:nth-child(n+2) {
  display: none;
}

.top-book-card {
  width: 100%;
  border-radius: 8px;
  margin-left: auto;
  margin-right: auto;
}

.book-card-thumb {
  width: 335px;
  color: var(--light-theme-primary-text-color);
  font-family: inherit;
  text-decoration: none;
}

.book-card-thumb .thumb {
  margin-bottom: 14px;
}

.thumb {
  width: 100%;
  height: 485px;
  position: relative;
}

.thumb img {
  object-fit: fill;
  height: 100%;
  width: 100%;
  border-radius: 8px;
  display: block;
}

.book-cart-title {
  letter-spacing: -.64px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
}

.book-card-author {
  color: var(--light-theme-second-text-color);
  letter-spacing: -.48px;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.17;
}

@media screen and (min-width: 768px) {
  .top-book-card:nth-child(-n+3) {
    display: block;
  }

  .books {
    margin-top: 98px;
    margin-bottom: 100px;
  }

  .category-name {
    letter-spacing: -1.92px;
    font-size: 48px;
    line-height: 1.083;
  }

  .books-container-list {
    column-gap: 25px;
  }

  .top-book-card {
    width: calc(33.3333% - 16.6667px);
  }

  .thumb {
    height: 316px;
  }
}

@media screen and (min-width: 1440px) {
  .top-book-card:nth-child(-n+5) {
    display: block;
  }

  .books {
    margin-top: 40px;
  }

  .books-container {
    width: 996px;
    padding: 0;
  }

  .category-name {
    letter-spacing: -1.92px;
    font-size: 48px;
    line-height: 1.083;
  }

  .book-card:-webkit-any(:hover, :focus) {
    transition: transform 250ms var(--transition-func);
    transition: box-shadow 250ms var(--transition-func);
    box-shadow: 0px 0px 11px -5px var(--light-theme-second-text-color);
    transform: scale(1.1);
  }

  .book-card:is(:hover, :focus) {
    transition: transform 250ms var(--transition-func);
    transition: box-shadow 250ms var(--transition-func);
    box-shadow: 0px 0px 11px -5px var(--light-theme-second-text-color);
    transform: scale(1.1);
  }

  .books-container-list {
    column-gap: 24px;
  }

  .top-book-card {
    width: calc(20% - 19.2px);
  }

  .thumb {
    height: 256px;
  }
}



body.dark-theme {
  background: #202024;
}

.category_list_element.dark-theme {
  color: rgba(255, 255, 255, .6);
}



